@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-progress-bar{
	padding-top:20px;
	padding-bottom:35px;
	.wrapper{
		background-size: 100% 100%;
	}

	.progress{
		position: relative;
		margin:0 1.6%;
		margin-top:-0.6%;
		&:before{
			content:'';
			display: block;
			padding-top:5.2%;
		}
		>*{
			.absolute(0,_,_,0);
			.size(100%);
		}

		&-end{
			.progress-nav{
				background-size:100% 100%;
				border-right:0 none;
			}
			.progress-num{
				display: none;
			}
		}
		&-hack .progress-nav{
			background-size:105% 100%;
		}
		&-txt{
			text-align: center;
			color:#fff;
			z-index:2;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		&-nav{
			z-index:1;
			border-right:1px #fff dotted;
			background-size: auto 100%;
		}
		&-num{
			text-align: center;
			.size(30px,36px);
			display: block;
			margin-top:5.5%;
			.absolute(_,_,_,-15px);
			color:#a56112;
			font-size:@font-size-sm  ;
			z-index:3;
			line-height: 3;
			background-image:data-uri('@{activity-image-path}/num.png');
			background-size: 100% 100%;
		}
	}
}
