@import "../_activity-variables.less";
@import "../_activity-mixins.less";


.fn-@{activity-ns}-pic-tab{
	.tabs{
		li{
			text-align: center;
			padding: 5px 0;
			color: @ht-color;
			width: 50%;
			background: #fff;
			line-height: 1.6;

			&.active{
				background: @ht-color;
				color: #fff;
			}
		}
	}

	.line{
		border-top: 1px solid @border-color-base ;
		overflow: hidden;

		a{
			float: left;
			width: 100%;
			position: relative;

			&:before{
				content: "";
				display: block;
				width: 100%;
				padding-top: 31.2%;
			}

			&:after{
				content: "";
				display: block;
				.size(1px, 100%);
				background: @border-color-base ;
				.absolute(0,_,_,0);
			}

			&:first-child{
				&:after{
					display: none;
				}
			}

			img{
				.absolute(0,_,_,0);
				.size(100%);
			}
		}

		&-4 a{
			width: 25%;

			&:before{
				padding-top: 125.1%;
			}
		}
	}
}
