@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-listpro-group{
	background: #fff;

	.item-list{
		.item{
			display: table;
			width: 100%;
			padding: 10px 0;
			border-bottom: 1px solid @border-color-base;
			position: relative;

			&:last-child{
				border-bottom: none;
			}

			.pic{
				display: table-cell;
				vertical-align: middle;
				width: 31.5%;

				span{
					display: block;
				}
			}

			.desc{
				display: table-cell;
				vertical-align: top;
				padding-left: 15px;
				position: relative;

				.title{
					display: block;
					line-height: 18px;
					height: 36px;
					overflow: hidden;
				}

				.buy{
					color: #fff;
					background: @price-color;
					padding: 5px;
					border-radius: 4px;

					&.no-sale{
						background: #999;
					}
				}
			}

			&-default{
				.price{
					.absolute(_,_,0,15px);
					font-family: arial;


					del{
						padding-left:2px;
						font-size: @font-size-sm;
						color: #999;
						text-decoration: line-through;
					}

				}

				.buy{
					.absolute(_,10px,-2px,_);
				}
			}

			&-presell{
				.pic .sub{
					background: rgba(51, 51, 51, 0.85);
					color: #fff;
					text-align: center;
					.absolute(_,_,0,0);
					width: 100%;
					.ellipsis();
					font-size: @font-size-sm;
					padding: 2% 0;
				}

				.info{
					color: #999;
					font-size: @font-size-sm;
					margin-top: 3px;

					.icon{
						padding-right: 5px;
					}

					&-mb{
						padding-bottom: 30px;
					}

					.ht{
						margin-left: 5px;
						color: @ht-color;
					}
				}

				.price{
					.absolute(_,_,0,15px);
					color: @ht-color;
				}

				.buy{
					.absolute(_,10px,-2px,_);
				}
			}

			&-score{
				.price{
					.absolute(_,_,0px,15px);
					width:100%;
					font-family: arial;

					.curr{
						margin-bottom:.32rem;
						.ellipsis();
						color:#999;
					}
					del{
						font-size: @font-size-sm;
						color: #999;
						text-decoration: line-through;

					}
				}
				.score{
					font-size:unit(@price-size-num-md,rem);
					strong{
						font-weight: normal;
						color:@price-color;
						padding-right:4px;
						font-size:unit(@price-size-num-lg ,rem);

					}
				}
				.buy{
					.absolute(_,10px,-2px,_);
				}
			}
		}
	}
}

@media(max-width:320px){
	.cms-page .cms-module-listpro-group .item-list .item-default .price .vip{
		&:before{
			padding:0;
			content:'';
		}
	}
	.fn-activity-listpro-group .item-list .item-default .price del{
		font-size:0;
	}
}
