@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-product-slide{
	background: #fff;

	.m-hd{
		padding: 10px;
		border-bottom: 1px solid @border-color-base;
	}

	.swiper-slide{
		width: 28.5%;
		display: block;
		border-left: 1px solid @border-color-base;

		&:first-child{
			border-left: none;
		}
	}

	.pic{
		width: 80%;
		margin: 0 auto;
		overflow: hidden;
		display: block;
		padding: 5% 0;

		.img-box{
			display: block;
		}
	}

	.title{
		padding: 0 10%;
		line-height: 1.6;
		height: 1.6 * @font-size-base * 2;
		.ellipsis(2);
		overflow: hidden;
		font-weight: 400;
	}

	.price{
		margin: 0 10% 5% 10%;
		overflow: hidden;
		height:@line-height-computed-base;

		.now{
			margin-right: 3px;
			display: inline-block;
			vertical-align: bottom;
		}

		del{
			color: #999;
			font-size: @font-size-sm ;
			display: inline-block;
			vertical-align: bottom;
			text-decoration: line-through;
		}
	}
}
