@import "../../cms/_plugin/cmp-item-list.less";

@import "../_activity-variables.less";
@import "../_activity-mixins.less";


.fn-@{activity-ns}-list2coupon-group{

	.item-v-list{
		.cmp-item-list();
	}

	.item{
		margin-top: 0;

		.title, .price{
			width: 100%;
		}

		&-coupon{

			.pic .sub{
				background: rgba(0, 0, 0, 0.3);
				color: #fff;
				text-align: center;
				.absolute(_,_,0,0);
				width: 100%;
				.ellipsis();
				padding: 1% 0;
			}
			.pic-rect{
				&.img-box:before{
					padding-top:117%;
				}
			}
			.price{
				display: block;
				.ellipsis();
				margin:5px auto 0 auto !important;

			}
			.tlt{
				.ellipsis();
				padding:0 10px;
				margin:5px 0;
			}
			.btn{
				display: block;
				text-align: center;
				background: @price-color;
				.ellipsis();
				width:70%;
				padding: 4px 0;
				border-radius: 4px;
				color: #fff;
				margin:0 auto 10px;
			}
			.coupon-box{
				margin:0 10px;

				.coupon{
					margin-top:1px;
					.size(46%,26px);
					float: left;
					line-height: 26px;
					border-left:1px @ht-color solid;
					border-right:1px @ht-color solid;
					background: #eee;
					position:relative;
					color:@ht-color;
					text-align: center;

					&:before{
						content:'';
						.absolute(-2px,_,_,0);
						.size(100%,3px);
						background: data-uri('@{activity-image-path}/list2coupon_group_before.png') no-repeat;
						background-repeat: repeat;
					}
					&:after{
						content:'';
						.absolute(_,_,-2px,0);
						.size(100%,3px);
						background: data-uri('@{activity-image-path}/list2coupon_group_after.png') no-repeat;
						background-repeat: repeat;
					}

					.q{
						width:30%;
						float: left;
						border-right:1px dotted @ht-color;
					}
					.y{
						margin-left:30%;
						display: block;
						overflow: hidden;
					}
				}

				.btn{
					width:46%;
					margin:0 0 10px;
					float: right;
				}
			}

			.logo{
				width:48%;
				margin:5px auto;

				&.img-box:before{
					padding-top:45%;
				}
			}
			.tips{
				line-height:20px;
				height:20px;
				color:@ht-color;
				.ellipsis();
				text-align: center;
				margin-bottom:5px;
			}

		}
	}
}
