@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-pic-countdown{
	.item{
		margin-top: 2%;
		display: block;
		position: relative;

		&.item-01:before{
			padding-top: 32%;
		}

		&.item-02:before{
			padding-top: 46.6%;
		}

		.slim-content{
			margin-top: 2.4%;
		}

		&:first-child{
			margin-top: 0;
		}

		.cd{
			.absolute(0,0,_,_);
			background: rgba(0, 0, 0, 0.6);
			padding: 5px;
			color: #fff;

			.icon{
				margin-right: 5px;
			}
		}
	}
}
