@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-collect-red{
	.collect-wrap{
		padding:2% 3.2% 0;
		position:relative;
		overflow:hidden;

		.item{
			width:32%;
			display: block;
			float:left;
			position:relative;
			margin-right: 2%;

			.collect-ranking{
				.absolute(-4px,_,_,10px;);
				background-image: data-uri('@{activity-image-path}/collect-ranking-bg.png');
				padding:3px 10px 10px 3px;
				color: #fff;
				text-align: center;
				font-size: @font-size-base;
				background-size:100% 100%;

				.top{
					display:block;
				}

				.num{
					display: block;
					margin-top: -5px;
					font-style: normal;
				}
			}

			.collect-bottom{
				.absolute(_,_,0,0);
				width: 100%;
				background: rgba(0,0,0, 0.6);
				padding:0 5px;
				text-align: center;
				color: #fff;
				line-height: 1.8;
				font-size: @font-size-base;
				.ellipsis();

				.icon-collect{
					color: @price-color;
				}
			}

			&:last-child{
				margin-right:0;
			}
		}
	}
}
