@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-headline{
	@head-height: 40;
	@head-padding: 5;
	@left-width: 75;

	.headline{
		height: unit(@head-height, px);
		background: #fff;

		.pic{
			.size(unit(@left-width, px), unit(@head-height, px));
			float: left;
			text-align: right;

			&:before{
				background: data-uri('@{activity-image-path}/activity_news_bg.png') no-repeat 0 0;
				display: inline-block;
				vertical-align: top;
				margin-top:10px;
				content: '';
				.size(63px, 20px);
				background-size: 100% 100%;
			}
		}

		.list{
			margin-left: unit(@left-width, px);

			.headline-swiper{
				height: unit(@head-height, px);

				.swiper-slide{
					a {
						display: block;
						margin-right:5px;
						line-height: unit(@head-height, px);
						padding-left: 5px;
						position: relative;
						.ellipsis();
						color:#999;
					}
				}
			}
		}
	}
}
