@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-offset-product{
	position:relative;
	padding: 3.2%;
	overflow: hidden;

	.offset{
		width: 56%;
		.absolute(0,_,_,0);

		.img-box{
			display: block;

			&:before{
				padding-top: 57%;
			}
		}
	}

	.item-img{
		overflow: hidden;
		.img-box{
			display: block;
			width: 49%;
			margin: 0 2% 2% 0;
			float: left;

			&:before{
				padding-top: 48.5%;
			}

			&:nth-child(even){
				margin-right: 0;
			}

			&.no-img{
				z-index: -1;
			}
		}
	}

	.item-text{
		overflow: hidden;

		.img-box{
			display: block;
			width: 23.5%;
			float: left;
			margin: 0 2% 0 0;
			position: relative;

			&:before{
				padding-top: 142.8%;
			}

			.text{
				.absolute(_,_,0,0);
				width: 100%;
				//.size(100%,38px);
				background: #000;
				opacity: .3;
				color: @body-background;
				padding: 8px 5px;
				.ellipsis();
				font-size: @font-size-sm;
				text-align: center;
			}

			&:last-child{
				margin-right: 0;
			}
		}
	}
}
