@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-seckill-slide{
	.swiper-container{
		background: #fff;
	}

	.swiper-slide{
		background: #fff;
		position: relative;
		text-align: center;
		padding: 3px 10px 4px 10px;
		width: 28.5%;
		border-bottom: 1px solid @border-color-base;

		p{
			.ellipsis();

			&.time{
				font-size:@font-size-lg ;
			}
			&.over{
				font-size:@font-size-sm;
				.count-down{
					padding-left:5px;
					em{
						font-style: normal;
					}
				}
			}
		}

		&.active{
			background: @ht-color;
			color:#fff;
			border-bottom-color: @ht-color;

			&:after{
				content:'';
				.absolute(_,_,-10px,50%);
				.size(0);
				line-height: 0;
				border-style: solid dashed dashed dashed;
				border-color: @ht-color transparent transparent transparent;
				border-width:5px;
			}
		}
	}

	.items2 .swiper-slide{
		width: 50%;
	}

	.items3 .swiper-slide{
		width: 33.33%;
	}

	.seckill{
		padding: 1.8% 3.2%;
		overflow: hidden;

		.title{
			float: left;
			color: @ht-color;
		}

		.cd{
			float: right;

			span{
				font-size: 14px;
			}

			.tip{
				color: #999;
				padding-right: 10px;
			}

			.num{
				background: #3d4245;
				color: #fff;
				border-radius: 3px;
				padding: 0 2px;
				font-family: arial;
			}

			.i{
				padding: 0 2px;
			}

		}
	}
}
