@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-coupon{
	.coupon{
		overflow: hidden;
		// margin-top: unit(@slim-mt, %);

		&:first-child{
			margin-top: 0;
		}
	}

	i{
		background: @container-bg-color;
		.size(10px);
		position: absolute;
		border-radius: 100%;
		z-index: 2;

		&.lt{
			left: -5px;
			top: -5px;
		}

		&.lb{
			left: -5px;
			bottom: -5px;
		}

		&.rt{
			right: -5px;
			top: -5px;
		}

		&.rb{
			right: -5px;
			bottom: -5px;
		}
	}

	.item{
		float: left;
		width: 100%;
		overflow: hidden;
		position: relative;
		margin-right: 2%;
		color: #fff;
		display: table;
		margin-bottom:2%;

		&:last-child{
			margin-right: 0;
		}

		.title{
			display: table-cell;
			width: 22.7%;
			text-align: center;
			position: relative;
			background: #707070;
			padding: 5px 3.8%;
			z-index: 1;
			vertical-align: middle;

			&:before{
				.absolute(0,-2px,_,_);
				height: 100%;
				width: 1px;
				border-left: 1px dashed @container-bg-color;
				content: '';
			}

			.price{
				font-size: 0.8rem;
				font-family: arial;
				line-height: 120%;
			}
		}

		.sub{
			font-size: @font-size-sm  ;
		}

		.desc{
			display: table-cell;
			width: 77.3%;
			padding: 5px 3.8%;
			background: #898989;
			position: relative;
			vertical-align: middle;

			.name{
				font-size: @font-size-xl   ;
				line-height: 1;
				padding: 3px 0 3px 0;
			}

			.tag{
				.absolute(0,_,_,0);
				.size(100%, 100%);
				background-size: auto 100%;
				background-repeat: no-repeat;
				background-position: center 0;

				&.cancelle{
					background-image: data-uri('@{activity-image-path}/coupon-cancelle.png');
				}

				&.collor{
					background-image: data-uri('@{activity-image-path}/coupon-collor.png');
				}

				&.expire{
					background-image: data-uri('@{activity-image-path}/coupon-expire.png');
				}

				&.used{
					background-image: data-uri('@{activity-image-path}/coupon-used.png');
				}
			}
		}

		&.active{
			.title{
				background: @ht-color;
			}

			.desc{
				background: @ht-color;
			}
		}

		&-es{
			.title .price{

			}

			.sub{
				font-size: @font-size-sm ;
			}
		}
	}

	.coupon-col-2{
		.item{
			width: 49%;

			.title{
				width: 41.2%;
			}

			.desc{
				width: 58.8%;
			}
		}
	}

	.coupon-col-3{
		.item{
			width: 32%;

			.title{
				width: 40.9%;
			}

			.desc{
				width: 59.1%;
			}
		}
	}
}
