@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-various-pic{

	.line{
		overflow: hidden;
		position:relative;

		i{
	 		display: block;
	 	}

	 	.img-wrap{
	 		.absolute(0,_,_,0);
 			.size(100%);

			> a{
			 	float: left;
			 	margin-right:1.4%;
			 	display: block;
			 	height:100%;
			 	position: relative;

			 	&:last-child{
			 		margin-right: 0;
			 	}

			 	img{
			 		.size(100%);
			 		.absolute(0,_,_,0);
			 	}
			}
	 	}

		&.col2{
		 	a {
		 		width: 49.3%;
		 	}
		}

		&.col3{
		 	a {
		 		width: 32.4%;
		 	}
		}

		&.col4{
		 	a {
		 		width: 23.95%;
		 	}
		}

		&.col5{
		 	a {
		 		width: 18.88%;
		 	}
		}
	}
}
