@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-video{
	.wrapper{
		background-color: #000;
		position: relative;
		overflow: hidden;

		&:before, &:after{
			content: '';
			display: block;
		}

		&:before{
		    padding-top: 50%;
		}

		.mask{
		    .absolute(0,_,_,0);
		    .size(100%);
		    z-index: 2;
			background-position: center;
			background-repeat: no-repeat;
			background-size: 100% auto;
			background-image: data-uri('@{activity-image-path}/video-start.png');
		}

		.media{
			.absolute(0,_,_,0);
			.size(1px);
		}

		&.wifi .mask{
			background-image: data-uri('@{activity-image-path}/video-wifi.png');
		}

		&.play{
			.mask{
				display: none;
			}

			.media{
				.size(100%);
			}
		}

		&.pause{
			.mask{
				display: block;
			}
		}
	}
}
