@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-group-slide{
	background: #fff;
	padding-top: 2.2%;

	.swiper-slide{
		> a{
			margin-left: 2.5%;
			width: 30%;
			position: relative;
			margin-top:3px;
		}
		.time{
			height:20px;
			line-height: 20px;
			padding:0 5px;
			background: @ht-color;
			color:#fff;
			.absolute(-3px,0,_,_);
			z-index:2;

			&:before{
				content:'';
				display: block;
				border-style:dashed dashed solid;
				border-color:transparent transparent #a00619;
				border-width: 2px;
				.absolute(1px,_,_,-2px);
				z-index:1;
				transform:rotate(135deg);
			}
		}
		.img-box{
			display: block;
			margin-bottom: 5%;
		}

		.title{
			padding: 0 5%;
			line-height: 1.6;
			height: @font-size-base * @line-height-base * 2;
			.ellipsis(2);
			overflow: hidden;
			font-weight: 400;
		}

		.price{
			margin: 2px 5%;
			overflow: hidden;
			height:@line-height-computed-base;

			.now{
				margin-right: 3px;
				display: inline-block;
				vertical-align: bottom;
			}

			del{
				color: #999;
				font-size: @font-size-sm;
				display: inline-block;
				vertical-align: bottom;
				text-decoration: line-through;
			}
		}
	}

	.swiper-container{
		padding-bottom:0.5rem;
	}
	.swiper-pagination{
		.absolute(_,0,0.2rem,_);
		z-index: 5;
		height: .16rem;
		width: 100%;
		text-align: center;

		> .swiper-pagination-bullet{
			display: inline-block;
			vertical-align: top;
			text-align: center;
			.size(.18rem);
			margin-right: .3rem;
			border-radius: 100%;
			border: .04rem solid @border-color-base;

			&-active{
				background: @ht-color;
				border: .02rem solid @ht-color;
			}
		}

	}
}
