@rolling-header-bg: #C70034;
@rollingheader-bg: #F70947;
@rollingheader-color: #fff;

.fn-@{activity-ns}-roll-header{
	background: @rolling-header-bg;
	padding: 10px 12px;

	.roll-header-box{
		background: @rollingheader-bg;
		overflow: hidden;
		position: relative;

		.box-tip{
			width: 120%/7.02;
			padding: 10px 5px 10px 10px;

			.img-box{
				display: block;

				&:before{
					padding-top: 425%/4.5;
				}

				.img{
					.size(100%,100%);
				}
			}
		}

		.box-main{
			.size(100%-120%/7.02,100%);
			position: absolute;
			top: 0;
			right: 0;
			overflow: hidden;

			.roll-box{
				height: 50%;
				overflow: hidden;

				&:first-child{
					border-bottom: 1px solid #c70034;
				}


			}

			.bg-red{
				background: #A5002B;
				padding: 2px 4px;
			}

			.bg-green{
				background: #16966A;
				padding: 2px 4px;
			}

			p{
				.ellipsis();
				color: @rollingheader-color;
				padding-left: 5px;

				span{
					line-height: 1rem;
					margin-right: 5*@rex;
					font-size: @font-size-base;
					color: @rollingheader-color;
				}
			}
		}
	}
}

