@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-sub-menu{
	.swiper{
		background: #fff;
	}

	.swiper-slide{
		display: block;
		text-align: center;
		font-size: @font-size-sm;
		padding: 10px 0;
		width: 25%;

		> span{
			display: block;
			font-size: @font-size-sm;
		}

		> i{
			font-size: 24px;
			display: block;
			line-height: @line-height-base;

			&.green{
				color: #33C7A4;
			}

			&.blue{
				color: #6679B2;
			}

			&.yellow{
				color: #F9B707;
			}

			&.red{
				color: #DA384C;
			}
		}
	}
}
