@import "../../cms/_plugin/cmp-item-list.less";

@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-prdlist-collect{
	overflow: hidden;

	.item-v-list{
		.cmp-item-list();
	}

	.info{
		width:100%;
		.absolute(_,_,0,_);
		z-index:1;
		transform:translate(0,50%);
		display: flex;
		align-items: flex-end;

		.logo{
			width:24%;
			margin-left:3%;
			display: block;
			border:1px @border-color-base  solid;
		}
		.tlt{
			width:73%;
			padding-left:5%;
			font-size:unit(@price-size-num-md,rem);
			.ellipsis();
		}
	}
	.sub{
		margin-top:12%;
		.ellipsis();
		padding:0 3%;
		color:#fff;
		line-height:2.2;
		color:@price-color;
		height:unit(2.2 * @price-size-num-md,rem);
	}
	.collect{
		background: @price-color;
		text-align: center;
		color:#fff;
		line-height:2.2;

		span:before{
			.iconfont-mixin(@activity-icon-name);
			content:'\e620';
			.inline-block();
			line-height: 2.2;
			vertical-align: top;
			padding-right:5px;
		}

		&-active{
			background: @text-color ;
		}
	}
	.item-long{
		float: left;
		margin-top:1.4%;
		overflow: hidden;
		&:before{
			padding-top:36%;
		}

		.item-wrap{
			.absolute(0,_,_,0);
			.size(100%);
		}
		.pic,.detail{
			width:50%;
			float: left;

			&:before{
				padding-top:72%;
			}
		}
		.detail{
			background: #fff;

			&-wrap{
				.absolute(_,_,0,0);
				width:100%;
			}
		}

		.info{
			bottom:100%;
			align-items: center;
		}

	}
}
