@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-list-complex{
	position: relative;
	&:before{
		padding-top: 70%;
		content: '';
		display: block;
	}

	.item{
		position: absolute;

		&.img-box:before{
			padding-top:100.20%;
		}
		&-l{
			top: 0;
			left: 0;
			.size(64.44%, 100%);

			&.img-box:before{
				padding-top:108.63%
			}
		}

		&-rt{
			top: 0;
			right: 0;
			.size(34.16%, 48.9%);
		}

		&-rb{
			bottom: 0;
			right: 0;
			.size(34.16%, 48.9%);
		}


	}

	.price{
		color: #fff;
		background: rgba(0,0,0,0.7);
		font-family: arial;
		.absolute(_,_,0,0);
		width:100%;
		line-height: 2;
		overflow: hidden;
		padding: 0 5%;

		.fn-price{
			float: left;
			color:#fff;
		}
		del{
			line-height: 2;
			float: right;
			text-decoration: line-through;
		}
	}
}
