@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-list-title{
	.title{
		display: block;
		margin-top: 2%;

		&.slim-content{
			margin-top:2%;
		}

		&:first-child{
			margin-top: 0;
		}
	}

	.text-title{
		text-align: center;
		font-size: 16px;
		color: #fff;
		padding: 6px 0;
	}

	.pic-title{
		position: relative;

		&:before{
			padding-top: 6.7%;
			display: block;
			content: '';
		}

		img{
			.absolute(0,_,_,0);
			.size(100%);
		}
	}
}
