@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-pic-mask{
	.line{
		overflow: hidden;
		margin-top: 2%;
		position: relative;

		&.slim-content{
			margin-top:2.14%;
		}

		&:first-child{
			margin-top: 0;
		}

		> a{
			float: left;
			width: 100%;
			margin-right: 1.4%;
			position: relative;

			&:last-child{
				margin-right: 0;
			}

			&:before{
				content: "";
				display: block;
				width: 100%;
			}

			img{
				.absolute(0,_,_,0);
				.size(100%);
			}
			&.auto{
				i{
					display: block;
				}
			}
			&.small{
				&:before{
					padding-top: 31.2%;
				}
			}

			&.medium{
				&:before{
					padding-top: 42.6%;
				}
			}

			&.big{
				&:before{
					padding-top: 61.3%;
				}
			}
		}

		.mask{
			.absolute(_,_,0,0);
			width:100%;
			line-height:  2;
			color:#fff;
			background: rgba(0,0,0,0.8);
			text-align: center;
			.ellipsis();

			span{
				color:#ffcf3c;
			}
		}
	}

	.line1-item4{
		> a{
			width:23.95%;

			&.item{
				position: relative;
				&:before{
					padding-top: 143%;
				}

				.mask{
					.absolute(_,_,0,0);
					width:100%;
					font-size:@font-size-sm;
					line-height: 2;
					color:#fff;
					background: rgba(0,0,0,0.8);
					text-align: center;
					.ellipsis();
				}
			}
		}
	}
}
