@import "../../cms/_plugin/cmp-item-list.less";

@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-list2presell-group{
	.item-v-list{
		.cmp-item-list();
	}

	.item{
		padding: 1.2% 1.2% 0 1.2%;
		margin-top: 0;

		.title, .price{
			width: 100%;
		}

		&-presell{
			.pic .sub{
				background: rgba(51, 51, 51, 0.85);
				color: #fff;
				text-align: center;
				.absolute(_,_,0,0);
				width: 100%;
				.ellipsis();
				padding: 1% 0;
			}

			.presell{
				margin-top: 5px;
			}

			.sub{
				color: @ht-color;
				.ellipsis();
			}

			.tip{
				color: #999;
				font-size: @font-size-sm;
				margin: 3px 0;
			}

			.btn{
				text-align: center;
				background: @price-color;
				.ellipsis();
				padding: 4px 0;
				border-radius: 4px;
				color: #fff;
				margin-bottom: 10px;
			}
		}
	}
}
