@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-tabs-list{

	@z-index: 199;
	@nav-size: 1.68;

	line-height: unit(@nav-size, rem);
	min-height: unit(@nav-size + 0.08, rem);

	.nav-wrap{
		max-width: unit(@container-max-width, px);
		margin: 0 auto;
		width: 100%;
		z-index: @z-index;
		top: 0;
		transform: translateZ(0);
	}

	.item-list{
		background: #fff;
		height:unit(@nav-size + 0.08, rem);

		.navs{
			border-right:1px solid @border-color-base;
			margin-right: unit(@nav-size, rem);
			height: 100%;
			position: relative;

			.list{
				a{
					padding: 0 10px;
					display: block;
					width: auto;
					position: relative;

					&.active{
						color: @ht-color;

						&:before{
							display: none;
						}

						&:after{
							display: block;
							content:'';
							.absolute(_,_,0,0);
							width: 100%;
						    height: 2px;
						    background: @ht-color;
						}
					}
				}
			}
		}

		.toggle-btn{
			float: right;
			.size(unit(@nav-size, rem), 100%);
			position: relative;
			transition: transform .3s ease;

			&:before,&:after{
				content: '';
				.absolute(50%,_,_,_);
				background: @text-color;
				.size(30%, 5%);
				transform-origin: 50% 50%;

			}

			&:before{
				left: 26%;
				transform: rotate(45deg);
			}

			&:after{
				right: 26%;
				transform: rotate(-45deg);
			}
		}
	}

	.sub-list{
		display: none;
	}

	.sub-category{
		overflow: hidden;
		line-height: 30px;
		padding-bottom: 10px;
		background: #fff;
		display: none;

		li{
			float: left;
			width: 25%;
			padding: 10px 8px 0 8px;

			> a{
				.ellipsis();
				text-align: center;
				display: block;
			}
		}

		li.active a{
			border-color: @ht-color;
			color: @ht-color;
		}

		.sub-collapse-btn, .sub-toggle-item{
			display: none;
		}

		&.expanded{
			.sub-collapse-btn, .sub-toggle-item{
				display: block;
			}

			.sub-expand-btn{
				display: none;
			}
		}
	}

	&.show{
		.item-list .toggle-btn{
			transform: rotate(180deg);
		}

		.sub-list{
			display: block;
		}

		.sub-category{
			display: block;
		}
	}

	&.fixed .nav-wrap{
		position: fixed;
	}
}
