@import "../_activity-variables.less";
@import "../_activity-mixins.less";


.fn-@{activity-ns}-pro-describe{
	
	background: @body-background;

	.describe-wrap{
		width: 94.6%;
		margin: 0 auto;	
		padding: 3% 0;

		.title{
			width: 100%;
			padding-bottom: 10px;
			display: flex;
			align-items: center;

			> * {
				display: table-cell;
			}

			.img-box{
				width: 24.8%;
				
				&:before{
					padding-top: 50%;
				}
			}

			.text{
				font-size: @font-size-base;
				color: @link-color;
				padding-left: 10px;
				.ellipsis();
			}
		}

		.detail{
			font-size: @font-size-sm;
			color: #666;
		}
	}

	

	
}
