@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-text-word{
	background: #fff;

	.hd{
		overflow: hidden;

		.hd-con{
			font-size: @font-size-xl    ;
			font-weight: 400;
			color:@link-color  ;

			padding-top: 20px;
			overflow: hidden;
		}

		.zan-wrap{
			overflow: hidden;
			border-bottom:1px #e8e8e8 solid;
			padding-bottom:10px;
			.time-con{
				width:50%;
				float: left;
				color:#999;
				padding-top:5px;
				font-size:@font-size-sm    ;
				line-height: 2;
				.ellipsis();
			}
			.item{
				float: right;
				text-align: left;
				padding-top:5px;
				width:60px;
				i{
					// line-height: 32px;
					font-size: @font-size-xxl ;
					display: block;
					.absolute(8px,_,_,0);
				}
			}
			.zan{
				position: relative;
				span{
					.absolute(8px,_,_,15px);
					background: @ht-color;
					padding:0 5px;
					text-align: center;
					color:#fff;
					border-radius: 8px;
					line-height: 16px;
					font-size:@font-size-sm;
				}
			}
		}
	}

	.bd{
		line-height: 1.4;
		padding-bottom: 10px;

		> p{
			padding:10px 0;
		}

		.pic{
			position: relative;
			overflow: hidden;

			&:before{
				content:'';
				display: block;
				padding-top:90%;
			}

			.img{
				.absolute(0,_,_,0);
				.size(100%);
				vertical-align: top;
			}

			.f-price{
				padding:5px 8px;
				background: #000;
				color:#fff;
				.absolute(_,0,30px,_);

				.fn-price{
					color:#fff;
				}
			}
		}
	}
}
