@import "../_activity-mixins.less";
@import "../_activity-variables.less";

.fn-@{activity-ns}-show-more{
	.show-more{
		display: block;
		border-top: 1px solid @border-color-base;
		border-bottom: 1px solid @border-color-base;
		background: #fff;
		padding: 5px 0;
		text-align: center;
		color: #666;

		span{
			margin-right: 5px;
		}

		i{
			margin-right: -20px;
		}

		&.slim-content{
			border: 1px solid @border-color-base;
		}
	}
}
