@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-label-line{
	.labels{
		margin-top: 1.5%;

		&:first-child{
			margin-top: 0;
		}

		a{
			font-size: @font-size-base  ;
			text-align: center;
			margin-right: 3.2%;
			background: #fff;
			border: 1px solid @container-bg-color;
			.ellipsis();
			padding: 5px 0;

			&:last-child{
				margin-right: 0;
			}
		}

		&-4 a{
			width: 22.6%;
		}

		&-3 a{
			width: 31.2%;
		}
	}
}
