@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-label-slide{
	@size: 44;
	@z-index: @activity-zindex-label-slide;

	.fixed-labels{
		min-height: unit(@size, px);

		&.active > .swiper-container{
			position: fixed;
		}
	}

	.swiper-container{
		background: #fff;
		z-index: @z-index;
		max-width: @container-max-width;
		top: 0;
		right: 0;
		left: 0;
		background: #fff;
		margin: 0 auto;
		transform: translateZ(0);
	}

	.list{
		margin-top: 2%;

		&:first-child{
			margin-top: 0;
		}
	}

	.swiper-slide{
		display: block;
		width: 28.5%;
		text-align: center;
		padding: 12px 0;
		position: relative;

		&:before{
			content: '';
			.absolute(50%,_,_,0);
			transform: translateY(-50%);
			.size(1px, 70%);
			background: @container-bg-color;
		}

		&:first-child{
			&:before{
				display: none;
			}
		}

		&.active{
			color: @ht-color;
		}
	}

	.items2{
		.swiper-slide{
			width: 50%;
		}
	}

	.items3{
		.swiper-slide{
			width: 33.3%;
		}
	}
}
