@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-pic-grid{
	.line{
		overflow: hidden;
		margin-top: 2%;

		&.slim-content{
			margin-top: 2.14%;
		}

		&:first-child{
			margin-top: 0;
		}

		> a{
			float: left;
			width: 100%;
			margin-right: 1.4%;
			position: relative;

			&:last-child{
				margin-right: 0;
			}

			&:before{
				content: "";
				display: block;
				width: 100%;
			}

			img{
				.absolute(0,_,_,0);
				.size(100%);
			}

			&.auto{
				i{
					display: block;
				}
			}

			&.small{
				&:before{
					padding-top: 31.2%;
				}
			}

			&.medium{
				&:before{
					padding-top: 42.6%;
				}
			}

			&.big{
				&:before{
					padding-top: 61.3%;
				}
			}
		}
	}

	.line1-item2{
		> a{
			width: 49.3%;

			&.small{
				&:before{
					padding-top: 41%;
				}
			}

			&.medium{
				&:before{
					padding-top: 49%;
				}
			}

			&.big{
				&:before{
					padding-top: 59.86%;
				}
			}
		}
	}

	.line1-item3{
		> a{
			width: 32.4%;

			&.small{
				&:before{
					padding-top: 75%;
				}
			}

			&.medium{
				&:before{
					padding-top: 91.7%;
				}
			}

			&.big{
				&:before{
					padding-top: 125%;
				}
			}
		}
	}
}
