@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-brand-slide{
	background: #fff;

	.cmp-swiper(55.7%);

	.swiper-container{
		padding-bottom:0.4rem;
	}

	.swiper-pagination .swiper-pagination-bullet {
		background:#fff;
		border:0.04rem solid #d5d5d5;

		&-active{
			background: @ht-color;
			border:0.04rem solid @ht-color;
		}
	}
	.swiper-pagination{
		text-align: center;
		position: relative;
		top:0.2rem;
		bottom:0;
		height:0.28rem;
	}

	.pro-line{
		width:100.2%;
		overflow: hidden;
		border-bottom:1px solid #eee;
		.logo{
			display: block;
			float: left;
			border-right:1px #eee solid;
			width:25%;
			&.img-box:before{
				padding-top:54%;
			}
		}

		&:first-child{
			border-top:1px solid #eee;
		}
	}
}
