@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-pic-slide{
	.swiper-slide{
		width: 28.5%;
		display: block;
		position: relative;

		&:before{
			display: block;
			content: "";
			padding-top: 150%;
		}

		&:after{
			content: "";
			display: block;
			.size(1px, 100%);
			background: @border-color-base ;
			.absolute(0,_,_,0);
		}

		img{
			.absolute(0,_,_,0);
			.size(100%);
		}

		&:first-child{
			&:after{
				display: none;
			}
		}
	}
}