@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-each-child{
	@item:26;
	@col:2.6;
	@width:@item * 3 + @col * 2;

	.bg{
		background-size: 100% auto;
		background-repeat: no-repeat;
	}

	.autoH{
		position: relative;

		&:before{
			content:'';
			display: block;
			padding-top:100%;
		}
	}


	.each-child{
		width:unit(@width,%);
		margin:0 auto;

		&-wrap{
			.absolute(0,_,_,0);
			.size(100%);
		}
		.item{
			width:unit(32,%);
			float: left;
			margin:0 unit(2,%) unit(2,%) 0;
			cursor: pointer;

			&:nth-child(3n){
				margin-right:0;
			}
			.img{
				.absolute(0,_,_,0);
				.size(100%);
				vertical-align: top;
			}
			.show-img{
				z-index:1;

			}
			.toggle-img{
				display: block;
				z-index:-1;
				background: data-uri('@{activity-image-path}/eachchild-none.png') center no-repeat;
				background-size: 100% 100%;

			}

			&.active-item{
				.toggle-img{
					z-index:2;
				}
			}
		}
	}
}
