@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-pic-combo{
	.line{
		border-top: 1px solid @container-bg-color;
		overflow: hidden;

		&:first-child{
			border-top: none;
		}

		a{
			float: left;
			width: 100%;
			position: relative;

			&:before{
				content: "";
				display: block;
				width: 100%;
				padding-top: 31.2%;
			}

			&:after{
				content: "";
				display: block;
				.size(1px, 100%);
				background: @container-bg-color;
				.absolute(0,_,_,0);
			}

			&:first-child{
				&:after{
					display: none;
				}
			}

			img{
				.absolute(0,_,_,0);
				.size(100%);
			}
		}

		&-2 a{
			width: 50%;

			&:before{
				padding-top: 62.4%;
			}
		}

		&-5 a{
			width: 20%;

			&:before{
				padding-top: 156%;
			}
		}
	}
}