@import "../../cms/_plugin/cmp-item-list.less";

@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-list2pro-group{
	.item-v-list{
		.cmp-item-list();
	}

	.item{
		padding: 1.2% 1.2% 0 1.2%;
		margin-top: 0;

		.title, .price{
			width: 100%;
		}
		.title{
			margin-bottom:0.1rem !important;
		}
		.phone{
			margin-left:5%;
			height:0.8rem;
		}

		.price{
			display: block;
			.ellipsis();

			del{
				max-width:34% !important;
			}

			.add-btn{
				font-size:0.84rem !important;
			}
		}

		&-pic{
			padding: 0;
			background: none;
			position: relative;

			&:before{
				content: "";
				display: block;
				padding-top: 100%;
				padding-bottom: 3.85rem;
			}

			.img{
				.absolute(0,_,_,0);
				.size(100%);
			}
		}

		&-presell{
			.pic .sub{
				background: rgba(51, 51, 51, 0.85);
				color: #fff;
				text-align: center;
				.absolute(_,_,0,0);
				width: 100%;
				.ellipsis();
				font-size: 12px;
				padding: 2% 0;
			}

			.info{
				font-size: @font-size-sm;
				color: #999;
				margin-top: 5px;

				.icon{
					margin-right: 5px;
				}

				.ht{
					margin-left: 5px;
					color: @ht-color;
				}

				&-mb{
					margin-top: 0;
					margin-bottom: 5px;
				}
			}

			.title{
				margin-bottom: 0;
			}

			.presell{
				margin-bottom: 10px;
				line-height: 22px;
				color: @ht-color;
			}
		}
	}
}

@media(max-width:320px){
	.fn-activity-list2pro-group .item-v-list .item .price del{
		font-size:0;
	}
}
