@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-pic1n6-grid{
	.line{
		position: relative;

		&:before{
			content: '';
			padding-top: 32%;
			display: block;
		}

		.wrap{
			.absolute(0,_,_,0);
			.size(100%);
		}

		border-bottom: 1px solid @border-color-base;

		&:last-child{
			border-bottom: none;
		}

		.item{
			float: left;
			border-left: 1px solid @border-color-base;
			.size(25%, 100%);

			&-big{
				width: 50%;
			}

			&:first-child{
				border-left: none;
			}

			&.img-box:before{
				padding-top:130%;
			}
		}
	}
}
