@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-red-pack-rain-entrance{
	position: relative;
	background-repeat: no-repeat;
	background-position: 0 0;
	background-size: 100% 100%;

	&:before{
		content:'';
		display:block;
		padding-top: 34.67%;
	}

	.red-pack-wrap{
		.absolute(0,_,_,0);
		.size(100%);
	}

	.red-pack-posi{
		.absolute(20%,0,0,10%);
		.size(80%);

		.red-pack-text{
			font-size: @font-size-xl;
			color:#fff;
			padding:  0 0 8px 5px;
		}

		.red-pack-start{
			padding:5px 10px;
			font-size: @font-size-base  ;
			background:#560415;
			color:#fff;
			display:inline-block;
			border-radius:50px;

			span{
				padding:0 5px;
			}
		}
	}
}
